*{
  margin: 0;
}

::-webkit-scrollbar{
  width: 10px;
}

::-webkit-scrollbar-thumb{
  background: rgba(14, 97, 190, 0.349);
  border-radius: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}

.App{
  background: #eee;
  width: 100%;
  height: auto;
  align-items: center;
  justify-content: center;
}

.navbar-custom{
  background-color: steelblue;
}

.navbar-link:hover{
  color: white;
  text-decoration: none;
}

.navbar-link{
  color: #eee;
  text-decoration: none;
}

.btn-primary{
  background: steelblue;
  outline-color: steelblue;
  outline-style: none;
  outline: none;
  border-color: steelblue;
}

.pagination{
  margin-top: 2vh;
  margin-bottom: 2vh;
  margin-left: 45%;
}

.btn-primary:hover{
  background: rgba(14, 97, 190, 0.349);
  border-color: rgba(14, 97, 190, 0.349);
}

.container{
  width: 100%;
  background: white;
  padding: 0;
  position: relative;
  margin-left: 2vh;
  margin-top: 2vh;
}

.header{
  text-align: center;
  background: steelblue;
  align-content: center;
  font-size: 8vh;
  width: 100%;
  color: white
}

.products-container{
  display: flex;
  flex-direction: row;
  background: white;
  width: 100%;
  flex-wrap: wrap;
  padding-left: 7%;
  padding-right: auto;
  padding-top: 2vh;
  padding-bottom: 5vh;
  margin: 0;
}

.product-card{
  width: 20%;
  height: auto;
  background: white;
  box-shadow: 0px 5px 10px #eee;
  border-radius: 2px;
  margin-right: 5px;
  margin-left: 25px;
  margin-top: 17.5px;
  margin-bottom: 17.5px;
}

.footer{
  bottom: 0;
  left: 0;
  display:flex;
  flex-direction: row;
  width: 100%;
  height: auto;
  background: steelblue;
  color: white;
  padding: 2%;
}

.inputsearch{
 position: absolute;
 right: 0;
 width: 20%;
 margin-right: 10%;
}

.filter-container{
  width: 100%;
  background: white;
  padding: 2%;
  overflow-y: auto;
}

.list{
  list-style: none;
  cursor: pointer;
  text-decoration: underline;
  margin-right: 50px;
  width: 100%;
}

.mainpanel{
  display: flex;
  flex-direction: row;
  background: #eee;
  padding-left: 10px;
  right: 0;
}

.filter{
  background: white;
  width: 20%;
  padding: 2%;
}

.filter-header{
  width: 100%;
  background: steelblue;
  padding: 2%;
  color: white;
  border-radius: 5px;
  margin-bottom: 10px;
}
.filter-title{
  background: #ccc;
  padding: 2%;
  border-radius: 5px;
  margin-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.maincontainer{
  width: 100%;
}

.product-title{
  font-weight: bold;
  font-size: 15px;
}

.product-price{
  font-weight: bold;
}

.product-initprice{
  color: red;
  font-weight: bold;
}

.product-lastprice{
  color: forestgreen;
  font-weight: bold;
}

.wsp-movil{
  display: none
}

.pagination-container{
  display: flex;
}

.float-cart-button{
  position: fixed;
  bottom: 2vh;
  right: 5vh;
  width: 7vh;
  height: 7vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: lightseagreen;
  color: white;
  border-radius: 100%;
  z-index: 2;
  box-shadow: 0px 5px 15px lightseagreen;
}

.home-page{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%
}

.home-sections{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px;
  opacity: 0;
  transition: all 2s;
}

.home-section{
  display: flex;
  justify-content: center;
  align-items: center;
  background: lightskyblue;
  width: 50vh;
  height: 50vh;
  border: 1px solid #fff;
  text-decoration: none;
  text-align: center;
}

.home-section:hover{
  cursor: pointer;
  background: skyblue;
  text-decoration: none;
}

.home-title{
  text-align: center;
  padding: 20px;
  opacity: 0;
  transition: all 2s;
}

.home-maps{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  opacity: 0;
  transition: all 2s;
}

.home-map{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 450px;
  height: 450px;
  padding: 20px;
}

.leaflet-container{
  width: 90%;
  height: 90%;
  z-index: 1;
}

.home-map-title{
  text-align: center;
}

@media (max-width: 550px){

  .home-sections{
    flex-wrap: wrap;
  }

  .home-section{
    width: 50%;
    height: 20vh;
  }

  .home-map{
    width: 100%;
  }

  .leaflet-container{
    width: 100%;
    height: 40vh;
    z-index: 1;
  }

  .container{
    width: 100%;
    height: auto;
    position: relative;
    left: 0%;
    transform: translateX(-0%);
    padding-left: 0;
    margin-right: 0;
    margin-left: 0;
  }

  .products-container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    padding-left: 1vw;
    padding-right: 1vh;
    padding-top: 2vh;
    padding-bottom: 2vh;
  }
  .product-card{
    width: 100%;
    height: auto;
    background: white;
    margin-left: 2vh;
  }
  .header{
    font-size: 5vh;
    width: 100%
  }
  .footer{
    flex-direction: column;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
  }
  .inputsearch{
    position: relative;
    width: 90%;
    margin-right: 0;
   }
  .filter-container{
    display: flex;
    flex-direction: row;
    width: 100%;
    background: white;
    padding: 2%;
  }
  .mainpanel{
    padding-left: 0;
    flex-direction: column;
  }
  .filter{
    width: 100%;
  }

  .filter-header::after{
    content:'>';
    font-weight: bold;
    position: relative;
    left: 68%;
  }

  .hide{
    display: none;
  }

  .lefilter{
    display: flex;
    flex-direction: row;
  }

  .list{
    text-decoration: none;
  }

  .pagination{
    margin-top: 2vh;
    margin-bottom: 2vh;
    margin-left: 0;
  }

  .wsp-web{
    display: none;
  }
  .wsp-movil{
    display: block;
  }

  .pagination-container{
    justify-content: center;
  }
}